<template>
  <div>
    <Loader v-show="!loaded" />
    <OnlineModal v-if="loaded && game.count === geojson.length" :score="game.score" :nickname="'You'" :enemy="'Bot'" :colors="sideColors" :makeRevenge="revenge" />
    <div class="modal-backdrop" v-if="loaded && enemyTurn"></div>
    <div v-if="loaded" id="map">
      <div v-if="game.count !== geojson.length">
        <div v-if="!enemyTurn" class="notification" :class="danger ? 'danger': ''">
          <p class="time"><span>Time: {{ seconds }}</span></p>
          <p class="attempts"><span>Attempts: {{ game.attempts }}</span></p>
          <div class="text--big" v-if="game.count !== geojson.length">
            {{ countries[this.game.count] }}
          </div>
          <ul class="users">
            <li class="user__score">
              <span>{{ users[0] }}</span>
              <span>{{ game.score.my }}</span>
            </li>
            <li class="user__score" :class="botGuess ? 'botGuess' : ''">
              <span>{{ users[1] }}</span>
              <span>{{ game.score.enemy }}</span>
            </li>
          </ul>
        </div>
        <div v-else class="notification">
          <div class="text--big enemyTurn">Opponent's Turn<span>.</span><span>.</span><span>.</span></div>
        </div>
      </div>
      <Map :geojson="geojson" :onEachFeature="onEachFeature" :botMode="botMode ? 'hardest' : ''" />
    </div>
  </div>
</template>
<script>
import Loader from '../components/Loader'
import OnlineModal from '../modals/OnlineModal'
import Map from '../components/Map'

export default {
  data () {
    return {
      geojson: [],
      layers: [],
      countries: [],
      game: {
        count: 0,
        attempts: 5,
        score: {
          my: 0,
          enemy: 0
        },
      },
      loaded: false,
      seconds: 15,
      interval: null,
      danger: false,
      users: ['You', 'Bot'],
      sideColors: {
        my: 'blue',
        enemy: 'tomato'
      },
      enemyTurn: false,
      chance: 0,
      botGuess: false
    }
  },
  computed: {
    botMode () {
      return this.$route.params.mode
    }
  },
  created () {
    this.getContinent()
    this.chance = this.$route.params.chance
  },
  methods: {
    getContinent () {
      if (this.$store.state.geojson) {
        this.geojson = [...this.$store.state.geojson]
        this.getCountries()
        this.loaded = true
        this.makeInterval()
      } else {
        this.$store.dispatch('getGeojson', 'full')
          .then(() => {
            this.geojson = [...this.$store.state.geojson]
            this.getCountries()
            this.loaded = true
            this.makeInterval()
          })
          .catch(error => {
            console.log(error)
          })
      }
    },
    getCountries () {
      this.countries = this.geojson.map(item => item.properties.name)
      this.countries.sort(this.compareRandom)
    },
    onEachFeature (feature, layer) {
      this.layers.push(layer)
      layer.bindPopup(layer.feature.properties.name)
      layer.on('click', () => {
        this.show(layer)
      }, this)
    },
    show (layer) {
      if (layer.feature.properties.name === this.countries[this.game.count]) {
        layer.setStyle({ fillColor: this.sideColors.my })
        layer.off('click')
        this.game.score.my++
        this.enemyTurn = true
        this.resetData()
        if (this.game.count !== this.geojson.length) {
          this.makeBotTurn()
        }
      } else {
        this.game.attempts--
        if (this.game.attempts === 0) {
          this.enemyTurn = true
          this.resetData()
          if (this.game.count !== this.geojson.length) {
            this.makeBotTurn()
          }
        }
      }
    },
    resetData () {
      clearInterval(this.interval)
      this.game.count++
      if (this.game.count === this.geojson.length) {
        return
      }
      this.game.attempts = 5
      this.seconds = 15
      this.danger = false
    },
    makeInterval () {
      this.interval = setInterval(() => {
        this.seconds--
        if (this.seconds === 3) {
          this.danger = true
        }
        if (this.seconds === 0) {
          this.enemyTurn = true
          this.makeBotTurn()
          this.resetData()
        }
      }, 1000)
    },
    makeBotTurn () {
      setTimeout(() => {
        let chance = Math.random()
        if (chance < this.chance) {
          this.layers.find(item => item.feature.properties.name === this.countries[this.game.count]).setStyle({ fillColor: this.sideColors.enemy }).off('click')
          this.game.score.enemy++
          this.botGuess = true
          setTimeout(() => {
            this.botGuess = false
          }, 1000)
        }
        this.resetData()
        if (this.game.count === this.geojson.length) {
          return
        }
        this.enemyTurn = false
        this.makeInterval()
      }, 2000)
    },
    revenge () {
      this.game.count = 0
      this.game.attempts = 5
      this.game.score.my = 0
      this.game.score.enemy = 0
      this.seconds = 15
      for (let i = 0; i < this.layers.length; i++) {
        this.layers[i].setStyle({ fillColor: '#fff' })
        if (!this.layers[i].listens('click')) {
          this.layers[i].on('click', () => {
            this.show(this.layers[i])
          }, this)
        }
      }
      this.enemyTurn = false
      this.makeInterval()
    }
  },
  components: {
    Loader,
    OnlineModal,
    Map
  }
}
</script>
<style scoped>
.modal-backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.5);
}
.notification {
  position: absolute;
  right: 50px;
  top: 50px;
  width: 300px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #404040;
  background-color: #fff;
  box-shadow: 0px 0px 11px rgba(0, 0, 0, 0.3);
  z-index: 1000;
}
.time, .attempts {
  position: absolute;
  top: 5px;
  margin: 0;
  font-size: 14px;
}
.time {
  left: 5px;
}
.attempts {
  right: 5px;
}
.text--big {
  user-select: none;
  font-size: 22px;
}
@media screen and (max-width: 400px) {
  .notification {
    right: 0;
    top: 0;
    width: 100%;
  }
}
.danger {
  animation: danger 1s infinite;
}
.users {
  width: 100%;
  padding: 0;
  list-style-type: none;
}
.user__score {
  display: flex;
  justify-content: space-between;
  padding: 5px 10px;
  color: #fff;
}
.user__score:nth-child(1) {
  background-color: rgba(0, 0, 255, 0.5);
  box-shadow: 0 0 10px blue;
}
.user__score:nth-child(2) {
  background-color: rgba(255, 99, 71, 0.5);
}
@keyframes danger {
  50% {
    box-shadow: 0 0 20px 5px inset tomato;
  }
}
.enemyTurn span {
  animation-name: blink;
  animation-duration: 1.4s;
  animation-iteration-count: infinite;
  animation-fill-mode: both;
}
.enemyTurn span:nth-child(2) {
  animation-delay: .2s;
}
.enemyTurn span:nth-child(3) {
  animation-delay: .4s;
}
.botGuess {
  animation: botGuess 1s;
}

@keyframes blink {
  0% {
    opacity: .2;
  }
  20% {
    opacity: 1;
  }
  100% {
    opacity: .2;
  }
}
@keyframes botGuess {
  50% {
    box-shadow: 0 0 10px 2px #91dc5a;
  }
  100% {
    box-shadow: 0 0 1px #91dc5a;
  }
}
</style>


