<template>
  <div>
    <div class="modal-backdrop"></div>
    <div class="modal">
      <div class="modal-content">
        <div class="status">
          <img class="status__img" src="../assets/images/global.svg" width="32" height="32" />
          You have been invited to game in mode <span class="mode__name">{{ modeName }}</span> by <span class="opponentColor">{{ opponent }}</span>
        </div>
      </div>
      <div class="buttons">
        <button class="btn--invite btn--blue" @click="makeDecision(true)">Accept</button>
        <button class="btn--invite btn--red" @click="makeDecision(false)">Decline</button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    opponent: {
      type: String
    },
    makeDecision: {
      type: Function
    },
    mode: {
      type: [Number, undefined, String]
    }
  },
  computed: {
    modeName () {
      switch (this.mode) {
        case 5000000:
          return 'More 5 millions'
        case 10000000:
          return 'More 10 millions'
        case 50000000:
          return 'More 50 millions'
        default:
          return 'All countries'
      }
    }
  }
}
</script>
<style scoped>
.modal-backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.5);
}
.modal {
  position: fixed;
  width: 100%;
  max-width: 500px;
  max-height: 100vh;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0 auto;
  padding-bottom: 3px;
  z-index: 1051;
  background: #fff;
  overflow-y: auto;
  box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.3);
}
.modal::before {
  background: linear-gradient(to right, #9BFFE6 0%, #A3CCFF 100%);
  content: "";
  display: block;
  height: 3px;
  width: 100%;
}
.modal-content {
  height: auto;
  position: relative;
  z-index: 1052;
  background: #fff;
  font-size: 24px;
}
.status {
  padding: 20px;
  border-bottom: 1px solid #e5e5e5;
}
.status__img {
  position: relative;
  top: 8px;
}
.buttons {
  margin-bottom: 20px;
  padding-top: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.mode__name {
  padding: 0 5px;
  background-color: #000;
  color: #fff;
}
.opponentColor {
  color: blue;
}
</style>


