import Vue from 'vue'
import Router from 'vue-router'
import Start from '../pages/Start'
import Game from '../pages/Game'
import Learn from '../pages/Learn'
import HardGame from '../pages/HardGame'
import Online from '../pages/Online'
import Bot from '../pages/Bot'
import Subject from '../components/Subject'
import ChooseContinent from '../components/ChooseContinent'
import ChooseCountry from '../components/ChooseCountry'
import CountryDetails from '../components/CountryDetails'
import CountryHistory from '../components/CountryHistory'

Vue.use(Router)

const router = new Router({
  mode: 'hash',
  base: '/',
  linkActiveClass: 'active',
  routes: [
    {
      name: 'Start',
      path: '/',
      component: Start
    },
    {
      name: 'Game',
      path: '/game',
      component: Game
    },
    {
      name: 'Learn',
      path: '/learn',
      component: Learn,
      redirect: '/learn/full',
      children: [
        {
          path: '/learn/continent',
          component: ChooseContinent,
          name: 'LearnContinent',
          children: [
            {
              path: '/learn/continent/:name',
              component: Subject,
              name: 'SubjectContinent',
              props: { mode: 'learn' }
            }
          ]
        },
        {
          path: '/learn/countries',
          component: ChooseCountry,
          name: 'LearnCountry',
          children: [
            {
              path: '/learn/countries/:name',
              component: Subject,
              name: 'SubjectCountry',
              props: { mode: 'learn' }
            }
          ]
        },
        {
          path: '/learn/details',
          component: CountryDetails,
          name: 'CountryDetails',
          props: { mode: 'details' }
        },
        {
          path: '/learn/history',
          component: CountryHistory,
          name: 'CountryHistory'
        },
        {
          path: '/learn/:name',
          component: Subject,
          name: 'Subject',
          props: { mode: 'learn' }
        }
      ]
    },
    {
      name: 'Hard',
      path: '/hard',
      component: HardGame
    },
    {
      name: 'Online',
      path: '/online',
      component: Online
    },
    {
      name: 'Bot',
      path: '/bot',
      component: Bot
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
})

export default router


