<template>
  <div>
    <Loader v-show="!loaded" />
    <div v-if="loaded" id="map">
      <div class="notification" :class="danger ? 'danger': ''">
        <p class="time"><span>Time: {{ seconds }}</span></p>
        <p class="right-answers"><span class="bold">{{ game.right_answers }}</span> of {{ geojson.length }}</p>
        <div class="country" v-if="game.count !== countries.length">
          {{ countries[game.count].name }}
        </div>
        <div class="digits">
          Score: {{ game.score }}
          <span class="attempts">
            Attempts: 
            <span>{{ game.attempts }}</span>
          </span>
        </div>
      </div>
      <Map :geojson="geojson" :onEachFeature="onEachFeature" />
    </div>
  </div>
</template>
<script>
import Loader from '../components/Loader'
import Map from '../components/Map'

export default {
  data () {
    return {
      geojson: [],
      countries: [],
      game: {
        count: 0,
        attempts: 5,
        score: 0,
        right_answers: 0
      },
      loaded: false,
      seconds: 15,
      interval: null,
      danger: false
    }
  },
  created () {
    this.getContinent()
    this.interval = setInterval(() => {
      this.seconds--
      if (this.seconds === 3) {
        this.danger = true
      }
      if (this.seconds === 0) {
        this.game.count++
        this.game.score--
        this.seconds = 15
        this.danger = false
      }
    }, 1000)
  },
  methods: {
    getContinent () {
      if (this.$store.state.geojson) {
        this.geojson = [...this.$store.state.geojson]
        this.makeCountries()
        this.loaded = true
      } else {
        this.$store.dispatch('getGeojson', this.name)
          .then(() => {
            this.geojson = [...this.$store.state.geojson]
            this.makeCountries()
            this.loaded = true
          })
          .catch(error => {
            console.log(error)
          })
      }
    },
    onEachFeature (feature, layer) {
      layer.bindPopup(layer.feature.properties.name)
      layer.on('click', () => {
        this.show(layer)
      }, this)
    },
    show (layer) {
      if (layer.feature.properties.name === this.countries[this.game.count].name) {
        this.game.score++
        this.game.right_answers++
        this.resetData()
        layer.setStyle({ fillColor: this.randomColor() })
        layer.off('click')
      } else {
        this.game.attempts--
        if (this.game.attempts === 0) {
          this.game.score--
          this.resetData()
        }
      }
    },
    resetData () {
      this.game.count++
      this.game.attempts = 5
      this.seconds = 15
      this.danger = false
    },
    makeCountries () {
      this.countries = this.geojson.map(item => item.properties.name)
      this.countries.sort(this.compareRandom)
    }
  },
  components: {
    Loader,
    Map
  }
}
</script>
<style scoped>
.notification {
  position: absolute;
  right: 50px;
  top: 50px;
  width: 300px;
  padding: 20px 20px 10px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #404040;
  background-color: #fff;
  box-shadow: 0px 0px 11px rgba(0, 0, 0, 0.3);
  z-index: 900;
}
.time {
  position: absolute;
  top: 5px;
  left: 5px;
  margin: 0;
  font-size: 14px;
}
.right-answers {
  position: absolute;
  top: 5px;
  right: 5px;
  margin: 0;
  font-size: 14px;
}
.country {
  font-size: 22px;
}
.digits {
  width: 100%;
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
  font-size: 18px;
}
@media screen and (max-width: 400px) {
  .notification {
    right: 0;
    top: 0;
    width: 100%;
  }
}
.danger {
  animation: danger 1s infinite;
}
@keyframes danger {
  50% {
    box-shadow: 0 0 20px 5px inset tomato;
  }
}
</style>


