<template>
  <div id="app">
    <router-view></router-view>
    <Invite v-if="isInvited" :opponent="opponent" :makeDecision="makeDecision" :mode="sort" />
  </div>
</template>

<script>
import Invite from './modals/Invite'

export default {
  name: 'app',
  data () {
    return {
      nickname: '',
      isInvited: false,
      opponent: '',
      mode: null
    }
  },
  mounted () {
    if (this.getCookie('name')) {
      this.nickname = JSON.parse(this.getCookie('name')).name
      this.$socket.emit('sendName', this.nickname)
    }
  },
  methods: {
    makeDecision (status) {
      this.$socket.emit('makeDecision', { myName: this.nickname, opponentName: this.opponent, status: status })
      if (status) {
        this.$router.push({
          name: 'Online',
          params: { 
            chooseOpponent: false,
            sort: this.sort
          }
        })
      }
      this.isInvited = false
    }
  },
  sockets: {
    getInvite (data) {
      this.isInvited = true
      this.opponent = data.myName
      this.sort = data.sort
      if (this.nickname !== data.opponentName) {
        this.nickname = data.opponentName
      }
    },
    declineInvite () {
      this.isInvited = false
    }
  },
  components: {
    Invite
  }
}
</script>

<style>
@import "~leaflet/dist/leaflet.css";

* {
  box-sizing: border-box;
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}
#app {
  min-height: 100vh;
  font-family: 'Montserrat', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.btn, .btn--invite {
  position: relative;
  width: 200px;
  height: 50px;
  line-height: 1;
  background: transparent;
  color: #2c3e50;
  border: 1px solid #2c3e50;
  border-radius: 5px;
  font-family: 'Michroma';
  font-size: 16px;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 2px;
  cursor: pointer;
  outline: none;
  overflow: hidden;
  transition: all 0.2s ease;
}
.btn:hover {
  background: #51667f;
  color: #fff;
}
.btn:disabled:hover {
  cursor: not-allowed;
  color: #2c3e50;
  background-color: #fff;
}
.btn:active {
  transform: translate(0px, 5px);
  -webkit-transform: translate(0px, 5px);
}
.btn:focus {
  box-shadow: 0 0 10px 1px blue;
}
.btn--start {
  background: linear-gradient(to bottom, transparent, black);
  color: #fff;
  border-color: #fff;
}
.btn--small {
  width: 150px;
}
.btn--sign {
  width: 100px;
  background-color: #fff;
  color: #2c3e50;
  border-width: 2px;
  font-size: 13px;
}
.btn--online {
  position: relative;
}
.btn--invite {
  margin-bottom: 20px;
}
.btn--invite:hover {
  box-shadow: 0 0 10px #5f5f5f;
}
.btn--blue {
  border: 1px solid #6eacf9;
  color: #6eacf9;
}
.btn--blue:hover:enabled {
  background-color: #6eacf9;
  color: #fff;
}
.btn--red {
  border: 1px solid rgb(255, 99, 71);
  color: rgb(255, 99, 71);
}
.btn--red:hover {
  background-color: rgb(255, 99, 71);
  color: #fff;
}
.fullHeight {
  min-height: 100vh;
}
.leaflet-container {
  background-color: #A3CCFF;
}
.leaflet-popup-content {
  text-align: center;
  font-size: 16px;
}
.bold {
  font-weight: bold;
  color: #107bff;
}
.guess_me {
  animation: guess 2s infinite;
}
.search__input {
  width: 200px;
  margin: 10px 0;
  padding: 5px 10px 5px 30px;
  border: 1px solid #2c3e50;
  outline: 0;
  border-radius: 5px;
  font: 15px 'Montserrat';
  transition: all 0.3s;
}
.search_icon {
  background-image: url('assets/images/search.svg');
  background-repeat: no-repeat;
  background-size: contain;
  background-size: 20px;
  background-position: 5px center;
}
.search__input:focus {
  box-shadow: 0 0 10px #2c3e50;
}
@keyframes guess {
  0% {
    fill-opacity: 1;
  }
  25% {
    fill-opacity: 0;
  }
  50% {
    fill-opacity: 1;
  }
  75% {
    fill-opacity: 0
  }
  100% {
    fill-opacity: 1;
  }
}
@keyframes tiltup {
  0% {
    bottom: -400px;
    opacity: 0;
    transform: rotateX(-90deg);
    border-top: solid 40px;
  }
  30% {
    opacity: 0;
  }
  100% {
    bottom: 0;
  }
}
@keyframes tiltbottom {
  0% {
    bottom: 0;
  }
  100% {
    bottom: -400px; 
    opacity: 0; 
    transform: rotateX(-90deg);
    border-top: solid 40px;
  }
}
</style>


