<template>
  <div>
    <Loader v-show="!loaded" />
    <DetailsModal v-if="isClicked" :country="selectedCountry" :close="closeModal" />
    <div class="learn_notification">
      <div class="selectors" :class="openNotification ? 'opened' : ''">
        <label>
          Population
          <br>
          <select @change="sortBy('pop_est')" v-model="sort.pop_est">
            <option value="all">All</option>
            <option value="-1000000">Less 1 m</option>
            <option value="-2000000">Less 2 m</option>
            <option value="-5000000">Less 5 m</option>
            <option value="5000000">More 5 m</option>
            <option value="10000000">More 10 m</option>
            <option value="50000000">More 50 m</option>
            <option value="100000000">More 100 m</option>
            <option value="min">Min</option>
            <option value="max">Max</option>
          </select>
        </label>
        <label>
          GDP
          <br>
          <select @change="sortBy('gdp')" v-model="sort.gdp">
            <option value="all">All</option>
            <option value="-1000">Less 1 k</option>
            <option value="-2000">Less 2 k</option>
            <option value="-5000">Less 5 k</option>
            <option value="5000">More 5 k</option>
            <option value="10000">More 10 k</option>
            <option value="20000">More 20 k</option>
            <option value="min">Min</option>
            <option value="max">Max</option>
          </select>
        </label>
        <label>
          Life
          <br>
          <select @change="sortBy('expectancy')" v-model="sort.expectancy">
            <option value="all">All</option>
            <option value="-40">Less 40 years</option>
            <option value="-50">Less 50 years</option>
            <option value="50">More 50 years</option>
            <option value="60">More 60 years</option>
            <option value="70">More 70 years</option>
            <option value="min">Min</option>
            <option value="max">Max</option>
          </select>
        </label>
        <label>
          Density
          <br>
          <select @change="sortBy('density')" v-model="sort.density">
            <option value="all">All</option>
            <option value="-10">Less 10</option>
            <option value="-50">Less 50</option>
            <option value="-100">Less 100</option>
            <option value="100">More 100</option>
            <option value="200">More 200</option>
            <option value="min">Min</option>
            <option value="max">Max</option>
          </select>
        </label>
        <label>
          Temperature
          <br>
          <select @change="sortBy('temperature')" v-model="sort.temperature">
            <option value="all">All</option>
            <option value="-0">Less 0 °C</option>
            <option value="-10">Less 10 °C</option>
            <option value="10">More 10 °C</option>
            <option value="20">More 20 °C</option>
            <option value="25">More 25 °C</option>
            <option value="min">Min</option>
            <option value="max">Max</option>
          </select>
        </label>
        <div class="squaredThree">
          Timezones
          <br>
          <input type="checkbox" value="None" id="squaredThree" name="check" @change="toggleTimezone" v-model="showTimezone"/>
          <label for="squaredThree"></label>
        </div>
        <form @submit.prevent="search" class="search">
          <div v-if="error" class="error">no such country</div>
          <input type="text" v-model="searchCountry" placeholder="Country" class="search_icon" @input="checkError">
          <br>
          <input type="submit" value="Search" class="btn">
        </form>
        <div v-if="!isMobile" class="count">Amount - {{ sortedGeojson.length }}</div>
      </div>
      <div class="opener" @click="openNotification = !openNotification">
        <img src="../assets/images/down.svg" width="16" height="16" :class="openNotification ? 'rotate' : ''">
      </div>
    </div>
    <div id="map" v-if="loaded">
      <Map :geojson="sortedGeojson" :onEachFeature="onEachFeature" :world="world" :timezones="timezones" :toggleTimezone="showTimezone" />
    </div>
  </div>
</template>
<script>
import Loader from './Loader'
import DetailsModal from '../modals/DetailsModal'
import Map from './Map'

export default {
  data () {
    return {
      value: '',
      geojson: [],
      world: [],
      layers: [],
      sortedGeojson: [],
      loaded: false,
      isClicked: false,
      selectedCountry: {},
      sort: {
        pop_est: 'all',
        gdp: 'all',
        expectancy: 'all',
        density: 'all',
        temperature: 'all',
      },
      searchCountry: '',
      error: false,
      openNotification: false,
      timezones: null,
      showTimezone: false
    }
  },
  created () {
    this.createGeo()
  },
  methods: {
    createGeo () {
      const getMap = new Promise((resolve, reject) => {
        this.$http.get('../json/map.json')
          .then(response => {
            resolve(response.body.features)
          })
          .catch(error => {
            reject(error)
          })
      })
      const getCustom = new Promise((resolve, reject) => {
        this.$http.get(`../json/custom.json`)
          .then(response => {
            resolve(response.body.features)
          })
          .catch(error => {
            reject(error)
          })
      })
      Promise.all([getMap, getCustom])
        .then(values => {
          this.world = [...values[0]]
          this.geojson = [...values[1]]
          this.sortedGeojson = [...this.geojson]
          this.loaded = true
        })
        .catch(error => {
          console.log(error)
        })
    },
    onEachFeature (feature, layer) {
      this.layers.push(layer)
      layer.setStyle({ fillColor: this.randomColor() })
      layer.on('click', () => {
        this.selectedCountry = layer.feature.properties
        this.isClicked = true
      })
    },
    closeModal () {
      this.isClicked = false
    },
    sortBy (type) {
      this.showTimezone = false
      this.sortedGeojson.length = 0
      if (this.sort[type] === 'max') {
        for (let key in this.sort) {
          if (key !== type) {
            this.sort[key] = 'all'
          }
        }
        let max = Math.max.apply(Math, this.geojson.map((o) => {
          return o.properties[type] ? +o.properties[type] : 0
        }))
        this.sortedGeojson.push(this.geojson.find((o) => {
          return +o.properties[type] === max
        }))
      } else if (this.sort[type] === 'min') {
        for (let key in this.sort) {
          if (key !== type) {
            this.sort[key] = 'all'
          }
        }
        let min = Math.min.apply(Math, this.geojson.map((o) => {
          return o.properties[type] ? +o.properties[type] : 9999999
        }))
        this.sortedGeojson.push(this.geojson.find((o) => {
          return +o.properties[type] === min
        }))
      } else {
        this.totalSort()
      }
    },
    totalSort () {
      this.sortedGeojson = [...this.geojson]
      for (let key in this.sort) {
        if (!['all', 'max', 'min'].includes(this.sort[key])) {
          if (Math.sign(this.sort[key]) === -1 || Math.sign(this.sort[key]) === 0) {
            this.sortedGeojson = this.sortedGeojson.filter(item => +item.properties[key] < Math.abs(this.sort[key]))
          } else {
            this.sortedGeojson = this.sortedGeojson.filter(item => +item.properties[key] > this.sort[key])
          }
        } else {
          this.sort[key] = 'all'
        }
      }
    },
    search () {
      const searchingCountry = this.layers.find(item => {
        return item.feature.properties.name.toLowerCase() === this.searchCountry.toLowerCase()
      })
      if (searchingCountry) {
        searchingCountry.fire('click')
        this.error = false
      } else {
        this.error = true
      }
    },
    checkError () {
      if (this.error) {
        this.error = false
      }
    },
    toggleTimezone () {
      if (!this.timezones) {
        this.$http.get(`../json/timezones.json`)
        .then(response => {
          this.timezones = [...response.body.features]
        }, response => {
          console.log(response)
        })
      }
    }
  },
  components: {
    Loader,
    DetailsModal,
    Map
  }
}
</script>
<style scoped lang="scss">
.learn_notification {
  position: absolute;
  width: 100%;
  font-size: 20px;
  z-index: 900;
}
.selectors > label {
  padding: 10px;
}
select, input:not([type="checkbox"]) {
  width: 100px;
  padding: 5px;
  border: 1px solid #2c3e50;
  outline: 0;
  border-radius: 5px;
  font: 12px 'Montserrat';
  transition: all 0.3s;
}
.search input {
  width: 150px;
  margin: 0;
  font-size: 15px;
}
.search {
  position: relative;
  padding: 10px;
}
.search__label {
  position: absolute;
  top: 5px;
  left: 5px;
  width: 20px;
  height: 20px;
  background-image: url('../assets/images/search.svg');
}
input {
  margin: 0;
}
input[type="text"] {
  padding: 5px 10px 5px 30px;
}
input[type="submit"] {
  height: auto;
  background: #51667f;
  color: #fff;
}
input[type="submit"]:hover {
  background-color: #354354;
}
select:focus, input:focus {
  box-shadow: 0 0 10px #2c3e50;
}
form {
  position: relative;
}
.error {
  position: absolute;
  top: -20px;
  padding: 1px;
  background-color: tomato;
  color: #fff;
  font-size: 14px;
  opacity: 0;
  animation: error 0.5s forwards;
}
@keyframes error {
  100% {
    opacity: 1;
  }
}
.selectors {
  overflow: hidden;
  transition: all 0.5s;
  height:0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  background-color: #9bffe6;
}
.opened {
  height: auto;
  padding: 20px;
}
.opener {
  position: absolute;
  bottom: -30px;
  width: 100%;
  height: 30px;
  line-height: 30px;
  background-color: #51667f;
  cursor: pointer;
}
img {
  transform: rotateX(0);
  transition: all 0.5s;
}
.rotate {
  transform: rotateX(180deg);
}

.squaredThree {
  position: relative;
  padding: 0 10px;
  label {
    width: 20px;
    height: 20px;
    cursor: pointer;
    position: absolute;
    bottom: 0;
    left: calc(50% - 10px);
    background: linear-gradient(top, #222 0%, #45484d 100%);
    border-radius: 4px;
    box-shadow: inset 0px 1px 1px rgba(0,0,0,0.5), 0px 1px 0px rgba(255,255,255,.4);
    &:after {
      content: '';
      width: 9px;
      height: 5px;
      position: absolute;
      top: 4px;
      left: 4px;
      border: 3px solid #fcfff4;
      border-top: none;
      border-right: none;
      background: transparent;
      opacity: 0;
      transform: rotate(-45deg);
    }
    &:hover::after {
      opacity: 0.3;
    }
  }
  input[type=checkbox] {
    visibility: hidden;
    &:checked + label:after {
      opacity: 1;
    }    
  }
}
</style>


