import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import VueResource from 'vue-resource'
import VueFire from 'vuefire'
import VueSocketio from 'vue-socket.io'

Vue.use(VueFire)
Vue.use(VueResource)
Vue.use(VueSocketio, process.env.PORT ? `0.0.0.0${process.env.PORT}` : '192.168.88.4:5000')

Vue.router = router
Vue.config.productionTip = false

const handler = () => {
  const WIDTH = 768
  const RATIO = 3
  const { body } = document
  if (!document.hidden) {
    const rect = body.getBoundingClientRect()
    return (rect.width - RATIO < WIDTH)
  }
}

Vue.prototype.getCookie = (name) => {
  let matches = document.cookie.match(new RegExp("(?:^|; )" + name.replace(/([.$?*|{}()[\]\\/+^])/g, '\\$1') + "=([^;]*)"))
  return matches ? decodeURIComponent(matches[1]) : undefined
}
Vue.prototype.compareRandom = () => {
  return Math.random() - 0.5
}
Vue.prototype.randomColor = () => {
  return `rgb(${Math.floor(Math.random() * 255)},${Math.floor(Math.random() * 255)},${Math.floor(Math.random() * 255)})`
}
Vue.prototype.isMobile = handler()

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

const makeServiceWorker = () => {
  if ('serviceWorker' in navigator) {
    window.addEventListener('load', () => {
      navigator.serviceWorker.register('/sw.js')
      .then(() => {
        console.log('ServiceWorker was registered successfully')
      })
      .catch((error) => {
        console.log(error)
      })
    })
  }
}

makeServiceWorker()


