<template>
  <div class="start-page">
    <BotDifficulty v-if="difficulty" :close="closeModal"/>
    <video v-if="!isMobile" class="start-bg" preload="auto" muted autoplay loop="loop">
      <source src="../assets/video/earth3.mp4" type="video/mp4">
    </video>
    <img v-else class="start-bg" src="../../public/img/body_bg.jpg" />
    <div class="brand">
      <h1 class="brand__name">
        Worldaria
        <div class='globe'></div>
      </h1>
      <SignForm v-if="!offline && start" :toggleSignError="toggleSignError" :setNickname="setNickname" />
    </div>
    <div class="start">
      <div v-if="!start" class="start__animation">
        <h2 class="start__heading" :class="startPressing ? 'lineMovement' : ''">Worldaria is the first educational geographical game in which you need to guess countries and even more!</h2>
        <button class="btn btn--start" @click="startPressed">Start</button>
      </div>
      <div v-if="start" class="left">
        <div class="mode game-mode">
          <span class="mode_name">
            Game mode
            <img src="../assets/images/information.svg" width="24" height="24" />
          </span>
          <div class="hidden">
            <p class="px5">In Game mode you have 5 attempts for each country. If you mark right, you will need to guess the capital of that country. Each right country and capital will give you 1 point. After 5 wrong attempts you will lose 1 point. You can save your current game anytime just quiting it.</p>
          </div>
        </div>
        <router-link to="/game" tag="button" class="btn btn--start">Play</router-link>
        <div class="mode hard-mode">
          <span class="mode_name">
            Hard mode
            <img src="../assets/images/information.svg" width="24" height="24" />
          </span>
          <div class="hidden">
            <p class="px5">In Hard mode you have only 15 seconds on each country. No capital guessing in this mode. After 15 seconds you will lose 1 point.</p>
          </div>
        </div>
        <router-link to="/hard" tag="button" class="btn btn--start">Hard</router-link>
        <div class="buttons">
          <div v-if="!offline">
            <div class="mode online-mode">
              <span class="mode_name">
                Online mode
                <img src="../assets/images/information.svg" width="24" height="24" />
              </span>
              <div class="hidden">
                <p class="px5">In Online mode you will play with real person one by one. Each player has 15 seconds to guess the country. After 5 wrong attempts you will not lose points.</p>
              </div>
            </div>
            <div class="btn--online">
              <div class="error error--sign" v-if="signInError">* sign in to play online</div>
              <router-link to="/online" tag="button" class="btn btn--start">Online</router-link>
            </div>
          </div>
          <div>
            <div class="mode online-mode">
              <span class="mode_name">
                Bot mode
                <img src="../assets/images/information.svg" width="24" height="24" />
              </span>
              <div class="hidden">
                <p class="px5">In Bot mode you will play with bot, whose difficulty you can choose. You have 15 seconds to guess the country.</p>
              </div>
            </div>
            <button class="btn btn--start" @click="chooseDifficulty">Bot</button>
          </div>
        </div>
        <p class="mb0">Play countries by their population</p>
        <div class="buttons">
          <router-link :to="{ name: 'Game', params: { sort: 5000000 }}" tag="button" class="btn btn--start btn--small mt">> 5 m</router-link>
          <router-link :to="{ name: 'Game', params: { sort: 10000000 }}" tag="button" class="btn btn--start btn--small mt">> 10 m</router-link>
          <router-link :to="{ name: 'Game', params: { sort: 50000000 }}" tag="button" class="btn btn--start btn--small mt">> 50 m</router-link>
        </div>
      </div>
      <div v-if="start" class="right">
        <div class="mode learn-mode">
          <span class="mode_name">
            Learn mode
            <img src="../assets/images/information.svg" width="24" height="24" />
          </span>
          <div class="hidden">
            <p class="px5">In Learn mode you have unlimited attempts. After 5 wrong attempts the right country will be accentuated. You can use search bar to find the right country.</p>
          </div>
        </div>
        <router-link to="/learn/full" tag="button" class="btn btn--start">Learn</router-link>
        <p class="mb0" v-if="!offline">Learn countries by</p>
        <div class="buttons" v-if="!offline">
          <router-link to="/learn/continent" tag="button" class="btn btn--start mt">Continents</router-link>
          <router-link to="/learn/countries" tag="button" class="btn btn--start mt">Regions</router-link>
        </div>
        <div class="mode details-mode">
          <span class="mode_name">
            Details mode
            <img src="../assets/images/information.svg" width="24" height="24" />
          </span>
          <div class="hidden">
            <p class="px5">In Details mode you can learn additional information about countries.</p>
          </div>
        </div>
        <router-link to ="/learn/details" tag="button" class="btn btn--start">Details</router-link>
        <div class="mode history-mode" v-if="!offline">
          <span class="mode_name">
            History mode
            <img src="../assets/images/information.svg" width="24" height="24" />
          </span>
          <div class="hidden">
            <p class="px5">In History mode you can learn historical countries.</p>
          </div>
        </div>
        <router-link v-if="!offline" to ="/learn/history" tag="button" class="btn btn--start">History</router-link>
      </div>
    </div>
  </div>
</template>
<script>
import BotDifficulty from '../modals/BotDifficulty'
import SignForm from '../components/SignForm'

export default {
  data () {
    return {
      startPressing: false,
      start: false,
      nickname: '',
      difficulty: false,
      signInError: false,
      offline: false
    }
  },
  created () {
    if (this.getCookie('name')) {
      this.nickname = JSON.parse(this.getCookie('name')).name
    }
    if (!navigator.onLine) {
      this.offline = true
    }
  },
  beforeRouteEnter (to, from, next) {
    if (from.name) {
      next(vm => {
        vm.start = true
      })
    }
    next()
  },
  beforeRouteLeave (to, from, next) {
    if (to.path === '/online') {
      if (!this.nickname) {
        this.signInError = true
        return
      }
    }
    next()
  },
  methods: {
    startPressed () {
      this.startPressing = true
      setTimeout(() => {
        this.start = true
      }, 1000)
    },
    setNickname (name) {
      this.nickname = name
    },
    chooseDifficulty () {
      this.difficulty = true
    },
    closeModal () {
      this.difficulty = false
    },
    toggleSignError () {
      this.signInError = false
    }
  },
  components: {
    BotDifficulty,
    SignForm
  }
}
</script>
<style scoped lang="scss">
.start-page {
  min-height: 100vh;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: baseline;
  background-color: rgba(0,0,0,0.5);
}
.start-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  object-fit: cover;
}
.start {
  width: 100%;
  display: flex;
  justify-content: center;
  color: #fff;
  overflow: hidden;
}
.start__heading {
  position: relative;
  width: 60%;
  margin: 0 auto 20px auto;
  padding: 20px;
  font-weight: normal;
  font-size: 26px;
}
.start__heading:before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 150%;
  height: 5px;
  background-color: #fff;
}
.start__heading:after {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  right: 0;
  width: 150%;
  height: 5px;
  background-color: #fff;
}
@media screen and (max-width: 500px) {
  .start__heading {
    width: 100%;
    font-size: 20px;
  }
}
.lineMovement {
  animation: opacityChanger 0.5s 0.5s reverse;
}
.lineMovement:before {
  animation: lineMovementLeft 1s cubic-bezier(.55,-0.43,.83,.67);
}
.lineMovement:after {
  animation: lineMovementRight 1s cubic-bezier(.55,-0.43,.83,.67);
}
@keyframes lineMovementRight {
  100% {
    transform: translateX(-100%);
  }
}
@keyframes lineMovementLeft {
  100% {
    transform: translateX(100%);
  }
}
.brand {
  position: relative;
  width: 100%;
  height: 70px;
  text-align: center;
  background-color: rgba(0,0,0,0.5);
  color: #fff;
}
.brand__name {
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 48px;
  font-family: 'Michroma', sans-serif;
  text-transform: uppercase;
  letter-spacing: -6px;
}
.globe {
  background-image: url('data:image/gif;base64,');
  transition-property: background-position, box-shadow;
  transition-duration: 0.7s;
  transition-timing-function: ease-in-out;
  cursor: pointer;
  width: 50px;
  height: 50px;
  border-radius: 50px;
  border: 2px solid #A3CCFF;
  box-shadow: inset 0px 0px 1px black,
              0px 0px 6px -1px black;
  background-size: auto 100%;
  background-repeat: repeat-x;
}
.globe:hover {
  background-position: -126px;
  box-shadow: inset 0px 0px 1px black,
              0px 0px 10px -1px black;
}
@media screen and (max-width: 1100px) {
  .brand__name {
    justify-content: flex-start;
    padding-left: 20px;
  }
}
@media screen and (max-width: 750px) {
  .brand {
    margin-bottom: 20px;
  }
  .brand__name {
    justify-content: center;
    padding-left: 0;
  }
}
.left, .right {
  width: 50%;
  padding: 20px 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  line-height: 1.6;
  font-size: 20px;
  opacity: 0;
  animation: opacityChanger 1s forwards;
}
.mode {
  position: relative;
  margin: 20px 0;
  font-size: 24px;
}
.mode img {
  position: relative;
  top: 3px;
  cursor: pointer;
}
.start__animation {
  padding: 10px 0;
}
.hidden {
  position: absolute;
  top: 35px;
  left: 0;
  z-index: 1;
  width: 300px;
  text-align: left;
  font-size: 16px;
  background-color: #fff;
  color: #2c3e50;
  border: 1px solid #2c3e50;
  border-radius: 5px;
  box-shadow: 0 0 5px #fff;
  visibility: hidden;
  opacity: 0;
  overflow: hidden;
  transition: visibility 0s, opacity 0.5s linear;
}
@media screen and (max-width: 500px) {
  .hidden {
    width: 200px;
  }
}
.mode__name {
  margin: 0;
  padding: 5px 10px;
  background-color: rgba(163, 204, 255, 0.3);
  border-bottom: 1px solid rgb(163, 204, 255);
}
.px5 {
  margin: 10px 0;
  padding: 0 10px;
}
.mode_name:hover ~ .hidden, .mode_name:active ~ .hidden {
  display: block;
  visibility: visible;
  opacity: 1;
}
.buttons {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.mb0 {
  margin-bottom: 0;
}
.mt {
  margin-top: 20px;
}
@media screen and (max-width: 650px) {
  .start {
    flex-wrap: wrap;
  }
  .left, .right {
    width: 100%;
    padding: 20px;
    font-size: 18px;
  }
  .right {
    padding: 20px 20px 40px;
  }
  .brand__name {
    font-size: 36px;
  }
}
</style>
<style>
.error {
  position: absolute;
  top: 0;
  left: 0;
  padding: 0 5px;
  font-size: 14px;
  background-color: tomato;
  color: #fff;
  opacity: 0;
  animation: opacityChanger 0.5s forwards;
}
.error--sign {
  top: -22px;
}
@keyframes opacityChanger {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
</style>


