<template>
  <div>
    <ChooseOpponent v-if="chooseOpponent" :users="onlineUsers" :nickname="nickname" :randomOpponent="randomOpponent" :sendInvite="sendInvite" :inviteSent="inviteSent" :opponentDecline="opponentDecline" />
    <Loader v-show="!loaded" />
    <OnlineModal v-if="enemyLeft || (loaded && game.count === countries.length)" :reason="reason" :nickname="nickname" :enemy="enemy" :score="game.score" :colors="sideColors" />
    <div class="modal-backdrop" v-if="loaded && enemyTurn"></div>
    <div v-if="loaded" id="map">
      <div v-if="!enemyLeft && game.count !== countries.length">
        <div v-if="!enemyTurn" class="notification" :class="danger ? 'danger': ''">
          <p class="time"><span>Time: {{ seconds }}</span></p>
          <p class="attempts"><span>Attempts: {{ game.attempts }}</span></p>
          <div class="text--big">
            {{ countries[this.game.count] }}
          </div>
          <ul class="users">
            <li v-for="(user, id) in users" :key="id" class="user__score" :class="user === nickname ? 'mySide' : enemyGuess ? 'enemyGuess' : ''">
              <span>{{ user }}</span>
              <span v-if="user === nickname">{{ game.score.my }}</span>
              <span v-else>{{ game.score.enemy }}</span>
            </li>
          </ul>
        </div>
        <div v-else class="notification">
          <div class="text--big enemyTurn">Opponent's Turn<span>.</span><span>.</span><span>.</span></div>
        </div>
      </div>
      <Map :geojson="geojson" :onEachFeature="onEachFeature" :world="world" />
    </div>
  </div>
</template>
<script>
import Loader from '../components/Loader'
import Map from '../components/Map'
import OnlineModal from '../modals/OnlineModal'
import ChooseOpponent from '../modals/ChooseOpponent'

export default {
  data () {
    return {
      geojson: [],
      world: [],
      layers: [],
      countries: [],
      onlineUsers: [],
      game: {
        count: 0,
        attempts: 5,
        score: {
          my: 0,
          enemy: 0
        },
      },
      loaded: false,
      seconds: 15,
      interval: null,
      danger: false,
      nickname: '',
      users: [],
      sideColors: {
        my: '',
        enemy: ''
      },
      enemyTurn: true,
      enemyGuess: false,
      enemyLeft: false,
      reason: '',
      chooseOpponent: true,
      opponentDecline: false,
      inviteSent: false,
      sort: null,
      opponentName: ''
    }
  },
  computed: {
    enemy () {
      return this.users.filter((item) => item !== this.nickname).toString()
    },
    invited () {
      return this.$route.params.chooseOpponent
    },
    sortNumber () {
      return this.$route.params.sort
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      if (!vm.getCookie('name')) {
        vm.$router.push('/')
      }
    })
  },
  created () {
    if (this.getCookie('name')) {
      this.nickname = JSON.parse(this.getCookie('name')).name
      this.$socket.emit('enterOnlineMode')
    }
    this.getContinent()
    if (typeof this.invited === 'boolean') {
      this.chooseOpponent = this.invited
    }
  },
  beforeRouteLeave (to, from, next) {
    if (this.game.count === this.geojson.length) {
      next()
      return
    }
    if (this.inviteSent) {
      this.$socket.emit('cancelInvite', { myName: this.nickname, opponentName: this.opponentName })
    }
    clearInterval(this.interval)
    this.$socket.emit('userLeft')
    next()
  },
  methods: {
    getContinent () {
      this.$http.get(`../json/map.json`)
        .then(response => {
          this.world = [...response.body.features]
          this.$http.get(`../json/full.json`)
            .then(response => {
              if (this.sortNumber) {
                this.geojson = response.body.features.filter(item => item.properties.pop_est > this.sortNumber)
              } else {
                this.geojson = [...response.body.features]
              }
            }, response => {
              console.log(response)
            })
        }, response => {
          console.log(response)
        })
    },
    onEachFeature (feature, layer) {
      this.layers.push(layer)
      layer.bindPopup(layer.feature.properties.name)
      layer.on('click', () => {
        this.show(layer)
      }, this)
    },
    show (layer) {
      if (layer.feature.properties.name === this.countries[this.game.count]) {
        layer.setStyle({ fillColor: this.sideColors.my })
        layer.off('click')
        this.$socket.emit('countryClick', true)
        this.game.score.my++
        this.enemyTurn = true
        this.resetData()
      } else {
        this.game.attempts--
        if (this.game.attempts === 0) {
          this.$socket.emit('countryClick', false)
          this.enemyTurn = true
          this.resetData()
        }
      }
    },
    resetData () {
      clearInterval(this.interval)
      this.game.count++
      if (this.game.count === this.geojson.length) {
        return
      }
      this.game.attempts = 5
      this.seconds = 15
      this.danger = false
    },
    makeInterval () {
      this.interval = setInterval(() => {
        this.seconds--
        if (this.seconds === 3) {
          this.danger = true
        }
        if (this.seconds === 0) {
          this.$socket.emit('countryClick', false)
          this.enemyTurn = true
          this.resetData()
        }
      }, 1000)
    },
    randomOpponent () {
      this.sort = null
      this.$socket.emit('createGame', this.nickname)
      this.chooseOpponent = false
    },
    sendInvite (name, sort) {
      if (this.inviteSent) {
        this.inviteSent = false
        this.$socket.emit('cancelInvite', { myName: this.nickname, opponentName: this.opponentName })
      } else {
        this.inviteSent = true
        this.opponentName = name
        this.opponentDecline = false
        if (sort) {
          this.sort = sort
        }
        this.$socket.emit('sendInvite', { myName: this.nickname, opponentName: this.opponentName, sort: sort })
      }
    },
    sortCountries () {
      this.geojson = this.geojson.filter(item => item.properties.pop_est && item.properties.pop_est > this.sort)
    }
  },
  sockets: {
    getOnlineUsers (data) {
      this.onlineUsers = [...data]
    },
    opponentsDecision (data) {
      if (data) {
        this.$socket.emit('createGame', this.nickname)
        this.chooseOpponent = false
      } else {
        this.opponentDecline = true
        this.inviteSent = false
      }
    },
    startGame (data) {
      if (this.sort) {
        this.sortCountries()
      }
      this.chooseOpponent = false
      this.users = [...data.users]
      this.countries = [...data.countries]
      this.loaded = true
      if (this.nickname === this.users[0]) {
        this.sideColors.my = 'blue'
        this.sideColors.enemy = 'tomato'
        this.enemyTurn = false
        this.makeInterval()
      } else {
        this.sideColors.enemy = 'blue'
        this.sideColors.my = 'tomato'
      }
    },
    checkAnswer (data) {
      if (data) {
        this.layers.find(item => item.feature.properties.name === this.countries[this.game.count]).setStyle({ fillColor: this.sideColors.enemy }).off('click')
        this.game.score.enemy++
        this.enemyGuess = true
        setTimeout(() => {
          this.enemyGuess = false
        }, 1000)
      }
      this.resetData()
      if (this.game.count === this.geojson.length) {
        return
      }
      this.enemyTurn = false
      this.makeInterval()
    },
    endMatch () {
      this.reason = 'leave'
      this.enemyLeft = true
      clearInterval(this.interval)
    },
    revengeGame (data) {
      this.game.count = 0
      this.game.attempts = 5
      this.game.score.my = 0
      this.game.score.enemy = 0
      this.seconds = 15
      this.countries = [...data]
      for (let i = 0; i < this.layers.length; i++) {
        this.layers[i].setStyle({ fillColor: '#fff' })
        if (!this.layers[i].listens('click')) {
          this.layers[i].on('click', () => {
            this.show(this.layers[i])
          }, this)
        }
      }
      if (this.nickname === this.users[0]) {
        this.enemyTurn = false
        this.makeInterval()
      }
    }
  },
  components: {
    Loader,
    Map,
    OnlineModal,
    ChooseOpponent
  }
}
</script>
<style scoped>
.modal-backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.5);
}
.notification {
  position: absolute;
  right: 50px;
  top: 50px;
  width: 300px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #404040;
  background-color: #fff;
  box-shadow: 0px 0px 11px rgba(0, 0, 0, 0.3);
  z-index: 1000;
}
.time, .attempts {
  position: absolute;
  top: 5px;
  margin: 0;
  font-size: 14px;
}
.time {
  left: 5px;
}
.attempts {
  right: 5px;
}
.text--big {
  user-select: none;
  font-size: 22px;
}
@media screen and (max-width: 400px) {
  .notification {
    right: 0;
    top: 0;
    width: 100%;
  }
  .time {
    left: auto;
    right: 5px;
    top: 20px;
  }
}
.danger {
  animation: danger 1s infinite;
}
.users {
  width: 100%;
  padding: 0;
  list-style-type: none;
}
.user__score {
  display: flex;
  justify-content: space-between;
  padding: 5px 10px;
  color: #fff;
}
.user__score:nth-child(1) {
  background-color: rgba(0, 0, 255, 0.5);
}
.user__score:nth-child(1).mySide {
  box-shadow: 0 0 10px blue;
}
.user__score:nth-child(2) {
  background-color: rgba(255, 99, 71, 0.5);
}
.user__score:nth-child(2).mySide {
  box-shadow: 0 0 10px tomato;
}
@keyframes danger {
  50% {
    box-shadow: 0 0 20px 5px inset tomato;
  }
}
.enemyGuess {
  animation: enemyGuess 1s;
}
@keyframes enemyGuess {
  50% {
    box-shadow: 0 0 10px 2px #91dc5a;
  }
  100% {
    box-shadow: 0 0 1px #91dc5a;
  }
}
</style>
<style>
.enemyTurn span {
  animation-name: blink;
  animation-duration: 1.4s;
  animation-iteration-count: infinite;
  animation-fill-mode: both;
}
.enemyTurn span:nth-child(2) {
  animation-delay: .2s;
}
.enemyTurn span:nth-child(3) {
  animation-delay: .4s;
}
@keyframes blink {
  0% {
    opacity: .2;
  }
  20% {
    opacity: 1;
  }
  100% {
    opacity: .2;
  }
}
</style>


